.mobile__menu {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background-color: rgba(128, 128, 128, 0.315);
  top: 0;
  left: 0;
}

.mobile__overlay {
  width: 320px;
  height: 100%;
  margin-right: auto;
  background-color: #ffffff;
  position: relative;
  padding-top: 2rem;
  padding-left: 1.5rem;
  animation: openAnimation 0.5s 1;
}

@keyframes openAnimation {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}
.mobile__overlay > div {
  display: block;
}
.mobile__overlay > div:last-child {
  margin-top: 1rem;
  border-bottom: none;
}
.mobile__overlay > div > ul {
  flex-direction: column;
}
.mobile__overlay li {
  border-bottom: 1px solid #e9e9e9;
  padding-bottom: 1.2rem;
}
.mobile__overlay li:last-child {
  border-bottom: none;
}
.mobile__menu-close {
  position: absolute;
  right: 21px;
  top: 35px;
}

@media only screen and (min-width: 600px) {
  .mobile__menu {
    display: none;
  }
}
