* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* header top */
header {
  width: 100%;
  background-color: #ffffff;
}

.header__top {
  display: flex;
  justify-content: center;
  margin: auto;
  width: 1160px;
  padding-top: 1.8rem;
  padding-bottom: 1.8rem;
  position: relative;
}

.header__logo {
  width: 180.83px;
  height: 26.6px;
}
.header__search {
  width: 18px;
  height: 18px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.header__top-menu {
  position: absolute;
  left: 2rem;
  top: 50%;
  transform: translateY(-50%);
  padding: 5px;
  display: none;
}
.header__top-menu img {
  width: 25px;
}
/* header menu  */
.header__menu, .hidden__menu {
  background-color: #ffffff;
  position: sticky;
  top: 0px;
  padding-top: 1.3rem;
  padding-bottom: 1.3rem;
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9;
}
.show__menu {
  animation: showMenu 0.5s 1;
  position: sticky;
}
.hidden__menu {
  animation: hideMenu 1s 1;
  position: static;
}

@keyframes showMenu {
  from {
    transform: translateY(-100%);
  }  
  to{
    transform: translateY(0%);
  }
}
@keyframes hideMenu {
  from {
    position: sticky;
    transform: translateY(0px);
  }
  to {
    transform: translateY(-200%);
  }
}

.header__menu > ul {
  justify-content: center;
  display: flex;
  list-style: none;
  gap: 2rem;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #000000;
  line-height: 1rem;
}
.header__menu-post {
  position: relative;
}

.header__menu-submenu {
  position: absolute;
  display: none;
  top: 1rem;
  left: 0px;
  z-index: 2;
  padding-top: 1.3rem;
}
.header__menu-submenu ul {
  width: 176px;
  cursor: pointer;
  height: 173px;
  list-style: none;
  border: 1px solid #e9e9e9;
  background: #ffffff;
  padding: 1.2rem;
}
.header__menu-post:hover .header__menu-submenu {
  display: block;
}
.header__menu-list li {
  color: rgb(0, 0, 0);
  font-family: "Roboto", sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.8rem;
  font-weight: 400;
  line-height: 0.8rem;
  letter-spacing: 0px;
  text-align: left;
}
.header__menu-list li img {
  width: 6.4px;
}
.header__menu-list li:first-child {
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgb(233, 233, 233);
}
.header__menu-list li:last-child {
  padding-top: 0.5rem;
}

.header__menu-item:nth-child(3) {
  color: rgb(150, 150, 150);
  letter-spacing: 0px;
  text-align: left;
  padding-left: 0.5rem;
}
.header__menu-item {
  padding: 0.5rem 0;
  padding: 0.5rem 0;
  border-bottom: 1px solid rgb(233, 233, 233);
}

.header__menu-link a {
  color: black;
  text-decoration: none;
}
.header__menu-down-icon {
  width: 10px;
  height: 6px;
}

@media only screen and (max-width: 1200px) {
  .header__top {
    width: 100%;
  }
  .header__search {
    right: 2rem;
  }
}
@media only screen and (max-width: 600px) {
  .header__menu {
    display: none;
  }
  .header__top-menu {
    display: block;
  }
}
