main {
  width: 1160px;
  margin: auto;
  margin-top: 3rem;
}

.post__container {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}

.post {
  width: 359px;
  cursor: pointer;
}

.post > img {
  width: 100%;
  height: 230px;
}
.post__style {
  color: rgb(235, 0, 40);
  font-family: "Roboto", sans-serif;
  font-size: 0.8rem;
  font-weight: 700;
  line-height: 13px;
  letter-spacing: 0px;
  text-align: left;
  margin: 1rem 0;
}
.post__title {
  color: rgb(0, 0, 0);
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0px;
  text-align: left;
  margin: 1rem 0;
}
.post__meta {
  margin: 1rem 0;
  display: flex;
  gap: 13px;
}
.post__text {
  color: rgb(146, 146, 146);
  font-family: "Roboto", sans-serif;
  font-size: 0.9rem;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0px;
  text-align: left;
}

.post__name {
  color: rgb(0, 0, 0);
  font-family: "Roboto", sans-serif;
  font-size: 0.75rem;
  font-weight: 500;
  line-height: 12px;
  letter-spacing: 0px;
  text-align: left;
}
.post__info {
  color: rgb(155, 155, 155);
  font-family: "Roboto", sans-serif ;
  font-size: 0.75rem;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0px;
  text-align: left;
}

.post__search {
  margin-bottom: 2rem;
  width: 300px;
  height: 25px;
}


/* popup */

.popup {
  width: 100vw;
  height: 100vh;
  background-color: rgba(128, 128, 128, 0.409);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.popup__content {
  width: 70%;
  background-color: white;
  display: flex;
  gap: 1rem;
  flex-direction: column;
  padding: 1.5rem;
}

@media only screen and (max-width: 1200px) {
  main {
    width: 90%;
  }
  .post > img {
    height: auto;
  }
  .post {
    flex-grow:1;
  }
}
